<template>
  <div id="app">
    <router-view></router-view>
    <a href="#">
      <div id="direction" @click.prevent="direction">
        <i class="glyphicon glyphicon-chevron-down"></i>
      </div>
    </a>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      list: [
        '.first', '.two'
      ],
      select: 0
    }
  },
  methods: {
    direction () {
      var select = this.select === 0 ? 1 : 0
      var $ = window.jQuery
      $(window).scrollTo(this.list[select], 2000)
      this.select = select
    }
  }
}
</script>

<style>
html, body, widow {
  padding: 0;
  margin: 0;
}
body{
  background: rgb(32, 160, 255);
}
.elm {
  padding: 0;
  height: 100vh;
  width: 100%;
  transition:background-color 1s ease; 
  position: relative;
}
#direction{
  padding: 10px;
  height: 50px;
  width: 50px;
  background: rgb(32, 160, 255);
  font-size: 20pt;
  color: #fff;
  border-radius: 60%;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: 2px solid #fff;
  text-align: center;
}
</style>



